 import React from "react";

 export const Modal = ({
    title,
    body,
    withCloseBtn,
    hasBgLayer,
    canCustomize,
    customStyles,
}) => {

    return (
        <div className={`flex h-screen items-center justify-center 
        ${canCustomize && hasBgLayer 
        ? "bg-gray-400"
        : ""
        }`}>

            <div className="mb-10 p-3 text-xl">
                <h3>{body}</h3>
            </div>
            <div className="flex gap-1 text-white">
                <button>Confirm</button>
                <button> No</button>
            </div>

        </div>
    )
};

